.pagination ul li {
  position: relative;
  width: 46px;
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  &:after {
    content: "";
    position: absolute;
    border: 2px solid transparent;
    width: 100%;
    height: 100%;
    z-index: 0;
    top: -2px;
    left: -2px;
    transform: skewX(-20deg);
  }
  a {
    border: none;
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

