.check label {
  cursor: pointer;
  display: inline-block;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 110%;
  /* or 15px */

  color: #324064;
  padding-left: 38px;
  position: relative;
}

.check input[type="checkbox"] {
  display: none;
}

.check label a {
  color: #25344f;
  text-decoration: underline;
}

.check label a:hover {
  text-decoration: none;
}

.check label:before {
  background-color: rgba(224, 149, 8, 1);
  border-radius: 4px;
  content: "";
  height: 18px;
  left: 0;
  position: absolute;
  width: 18px;
  top: 50%;
  transform: translateY(-50%);
}

.check input[type="checkbox"]:checked + label:before {
  background-image: url(../img/checkbox_checked.svg);
  background-repeat: no-repeat;
  background-position: center;
}

